.main {
  &__catalog {
    margin: 0 auto;
  }
}

.catalog {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: catalogWidth($card-width, $card-margin-rigth);
  padding: 50px 0;

  &__card {
    &:not(:nth-last-child(-n + 4)) {
      margin-bottom: $card-margin-bottom;
    }

    &:not(:nth-child(4n)) {
      margin-right: $card-margin-rigth;
    }
  }
}
